import request from '@/utils/request'

/*  */
export const list = data => request({ url: '/ktdb/projectAdmission/list', method: 'post', data })
/*  */
export const proDelete = data => request({ url: '/ktdb/projectAdmission/delete', method: 'post', data })
/*  */
export const getProjectById = data => request({ url: '/ktdb/projectAdmission/getProjectById', method: 'post', data })
/*  */
export const importFile = data => request({ url: '/ktdb/projectAdmission/import', method: 'post', headers: { 'Content-Type': 'multipart/form-data' }, data })
/* excel */
export const exportFile = data => request({ url: '/ktdb/projectAdmission/export', method: 'get', data })
/*  */
export const addPro = data => request({ url: '/ktdb/projectAdmission/add', method: 'post', data })
/* wordbyid */
export const exportWordById = data => request({ url: '/ktdb/projectAdmission/exportWordById', method: 'post', data })
// 
export const downlloadFileApiUrl = data => request({ url: '/ktdb/projectAdmission/exportExcel', method: 'post', data })
// 
export const exportWord = data => request({ url: '/ktdb/projectAdmission/exportWord', method: 'post', data })
