<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" ref="getQuery"  style="width: calc(100% - 70px)"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" style="margin-right: 10px" class="btn-add" icon="md-add" @click.stop="openAddModal"></Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- -->
    <div class="changeDetectTableMap" :class="{addActive:addActive}">
      <changeDetectTableMap ref="changeDetectTableMap" :seeId="taskId" @signOut="signOut" :actid="actid"></changeDetectTableMap>
    </div>
    <Modal v-model="modal1"
           title=""
           :mask-closable="false"
           >
      <Form :model="addForm" :label-width="130" ref="addForm" :rules="addFormRules">
        <FormItem prop="name" label="">
          <Input v-model="addForm.name" placeholder=""></Input>
        </FormItem>
        <FormItem prop="image1Id" label="">
          <i-select v-model.trim="addForm.image1Id" style="width: 100%" placeholder="">
            <i-option v-for="item in activitiesList1" :value="item.id">{{ item.name }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="image2Id" label="">
          <i-select v-model.trim="addForm.image2Id" style="width: 100%" placeholder="">
            <i-option v-for="item in activitiesList2" :value="item.id">{{ item.name }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="remark" label="">
          <Input v-model="addForm.remark" type="textarea"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false"></Button>
        <Button type="primary" @click="submitAddForm"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import changeDetectTableMap from './changeDetectTable_map'
import { renderTime } from '@/utils/common'
import { getChangeList, getRemoteList2, addTask, getActivitiesList2, getActivityChangeList, delActivityChangeRask } from '@/api/remoteSensing'
export default {
  name: 'changeDetectMap',
  data () {
    return {
      /*  */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /*  */
      queryData: {},
      /*  */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'name'
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          minWidth: 140,
          render: (h, params) => {
            return h('span', renderTime(params.row.date))
          }
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'image1'
        },
        {
          title: '',
          align: 'center',
          ellipsis: true,
          minWidth: 200,
          key: 'image2'
        },
        {
          title: '',
          width: 250,
          key: '',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 
              h('Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small',
                    icon: 'ios-aperture'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.newcom(params)
                    }
                  }
                }, ''),
              // 
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    icon: 'ios-map'
                  },
                  style: {
                    marginRight: '5px',
                    background: 'rgb(16,186,204)',
                    borderColor: 'rgb(16,186,204)'
                  },
                  on: {
                    click: () => {
                      this.addActive = true
                      this.$refs.changeDetectTableMap.init(params.row.id)
                      this.actid = params.row.id
                    }
                  }
                }, ''),
              h('Button',
                {
                  props: {
                    type: 'error',
                    size: 'small',
                    icon: 'md-trash'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '',
                        content: '',
                        onOk: () => {
                          this.proDelete(params.row.id)
                        },
                        onCancel: () => {
                        }
                      })
                    }
                  }
                },
                ''
              )
            ])
          }
        }
      ],
      data1: [],
      actid: '',
      /*  */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /*  */
      loading: false,
      /*  */
      addActive: false,
      /* id */
      taskId: 0,
      /*  */
      modal1: false,
      activitiesList1: [],
      activitiesList2: [],
      /*  */
      addForm: {
        name: '',
        image1Id: '',
        image2Id: '',
        remark: ''
      },
      // --
      addFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        image1Id: [
          { required: true, message: '', trigger: 'change', type: 'number' }
        ],
        image2Id: [
          { required: true, message: '', trigger: 'change', type: 'number' }
        ]
      }
    }
  },
  components: {
    QueryBox,
    // 
    changeDetectTableMap
  },
  computed: {
    // 
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: ''
        }
      ]
    }
  },
  created () {
    this.getTableList()
    // this.getRemoteList()
    this.getActivities2()
  },
  mounted () {
    // table
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /*  */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* ---- */
    getActivities2 () {
      getActivitiesList2({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.activitiesList1 = res.data.data
          this.activitiesList2 = res.data.data
        }
      })
    },
    /*  */
    getTableList () {
      let name
      if (this.queryData.name === undefined) {
        name = ''
      } else {
        name = this.queryData.name
      }
      this.loading = true
      getChangeList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /*  */
    signOut (addActive) {
      this.addActive = addActive
    },
    /*  */
    openAddModal () {
      this.$refs.addForm.resetFields()
      this.addForm = {}
      this.modal1 = true
    },
    /*  */
    submitAddForm () {
      this.$Spin.show()
      this.$refs.addForm.validate(valid => {
        if (valid) {
          addTask({
            data: {
              name: this.addForm.name,
              image1Id: this.addForm.image1Id,
              image2Id: this.addForm.image2Id,
              remark: this.addForm.remark
            }
          }).then(res => {
            this.$Spin.hide()
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              // ----
              this.$refs.addForm.resetFields()
              this.addForm = {}
              this.modal1 = false
              this.getTableList()
            } else {
              this.$Notice.error({ title: '' })
            }
          })
        }
      })
    },
    /*  */
    proDelete (id) {
      delActivityChangeRask({
        data: {
          id
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '' })
          this.getTableList()
        } else {
          this.$Notice.error({ title: '', desc: res.data.msg })
        }
      })
    },
    /*  */
    newcom (params) {
      this.$Spin.show()
      addTask({
        data: {
          name: params.row.name,
          image1Id: params.row.image1_id,
          image2Id: params.row.image2_id,
          remark: params.row.remark,
          id: params.row.id
        }
      }).then(res => {
        this.$Spin.hide()
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '' })
        } else {
          this.$Notice.error({ title: '' })
        }
      }).catch(() => {
        this.$Spin.hide()
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .changeDetectTableMap {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
